  border-radius: 4px;
  border: 1px solid var(--color-light-200);
  box-shadow: 0 8px 24px rgba(0, 23, 49, 0.2), 0 2px 4px rgba(0, 23, 49, 0.1), inset 0 0 0 1px rgba(255, 255, 255, 0.2);
