.formTabs {
  position: relative;
  box-sizing: border-box;
  overflow: visible;
}

.formTabs .progressbars {
  text-align: center;
  margin: 40px 0 20px 0;
  overflow: hidden;
  padding: 0px 80px;
}

.formTabs .progressbars li {
  list-style-type: none;
  color: var(--color-dark);
  float: left;
  position: relative;
}

.formTabs .progressbars li:before {
  content: counter(step);
  counter-increment: step;
  width: 20px;
  line-height: 20px;
  display: block;
  border-radius: 50%;
  margin: 0 auto 5px auto;
  z-index: 2;
}

.formTabs .progressbars li:after {
  content: "";
  width: 100%;
  height: 2px;
  position: absolute;
  left: -50%;
  margin-left: 10px;
  top: 9px;
  z-index: 1;
}

.formTabs .progressbars li:before,
.formTabs .progressbars li:after {
  background: var(--color-light-300);
  color: var(--color-light-300);
}

.formTabs .progressbars li:not(.active) {
  color: var(--color-light-400);
}

.formTabs .progressbars li:first-child:after {
  content: none;
}

.formTabs .progressbars li.active:before,
.formTabs .progressbars li.active:after {
  background: #00879e;
  color: #00879e;
}

.formTabs [data-index="1"].active ~ .b1-back {
  display: flex !important;
  font-size: 22px;
}

.pagination > .active > a,
.pagination > .active > span,
.pagination > .active > a:hover,
.pagination > .active > span:hover,
.pagination > .active > a:focus,
.pagination > .active > span:focus {
  background-color: #00879e !important;
  border-color: #00879e !important;
}

.pagination > li > a,
.pagination > li > span {
  position: relative;
  float: left;
  padding: 6px 12px;
  margin-left: -1px;
  line-height: 1.42857143;
  color: #00879e;
  text-decoration: none;
  background-color: var(--color-light);
  border: 1px solid #ddd;
}

