  border-radius: 8px;
}
.b1-content {
  padding: 32px;
}
.b1-form-container {
  max-width: 640px;
  padding: 32px;
  width: 100%;
  margin: 0 auto;
}
.b1-head-tabs {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 16px;
  height: 48px;
}
.b1-head-tabs__item {
  height: 48px;
  border-radius: 8px;
  margin-left: 4px;
  transition: all 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.b1-head-tabs__item:first-child {
  margin-left: 0px;
}
.b1-head-tabs__item:hover {
  background-color: rgba(0, 135, 158, 0.1);
}
.b1-head-tabs__item.active {
  background-color: rgba(0, 135, 158, 0.2);
}
.b1-head-tabs__link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 16px;
  height: 100%;
  text-transform: lowercase;
}
.b1-head-tabs__link:hover {
  opacity: 0.8;
  color: var(--color-dark);
}
.b1-head-tabs__item.active .b1-head-tabs__link {
  opacity: 1;
}
.b1-header-block {
  position: relative;
  display: block;
  padding: 16px;
  background-color: var(--color-light);
  color: var(--color-dark);
}
.b1-header-block__title {
  display: flex;
  align-items: center;
  position: absolute;
  top: -64px;
  left: -1px;
  padding: 16px 32px;
  background-color: var(--color-light);
  text-transform: lowercase;
  border-radius: 8px;
}
.b1-header-block__content {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.b1-header-block__item {
  display: flex;
  align-items: center;
  margin: 8px 0;
}
.b1-header-filters {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 16px 0;
}
.b1-header-filters__item {
  display: inline-block;
  margin-right: 4px;
  border-radius: 4px;
  transition: all 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.b1-header-filters__item:hover {
  background-color: rgba(0, 135, 158, 0.1);
}
.b1-header-filters__item.active {
  background-color: rgba(0, 135, 158, 0.2);
}
.b1-header-filters__link {
  display: block;
  padding: 8px 16px;
  cursor: pointer;
  transition: all 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.b1-header-filters__link:hover {
  color: var(--color-dark);
}
.b1-header-filters__link:hover {
  opacity: 0.8;
}
.b1-filter-block {
  padding: 8px 16px;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.b1-filter-block__item {
  display: inline-block;
  border: 1px solid transparent;
  border-radius: 16px;
  margin-right: 8px;
  transition: all 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.b1-filter-block__item:hover {
  background-color: rgba(0, 135, 158, 0.1);
}
.b1-filter-block__item.active {
  background-color: rgba(0, 135, 158, 0.2);
  border-color: rgba(0, 135, 158, 0.4);
}
.b1-filter-block__link {
  display: block;
  padding: 5px 16px;
  transition: all 0.15s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.b1-filter-block__link:hover {
  opacity: 0.8;
  color: var(--color-dark);
}
.b1-stick-top {
  position: fixed;
  z-index: 999;
  background-color: var(--color-light);
  border-radius: 4px;
  border: 1px solid var(--color-light-200);
  box-shadow: 0 8px 24px rgba(0, 23, 49, 0.2), 0 2px 4px rgba(0, 23, 49, 0.1), inset 0 0 2px rgba(255, 255, 255, 0.2);
}
.b1-button-block {
  padding: 8px 16px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.b1-button-block__item {
  display: flex;
  align-items: center;
  margin: 8px 0;
}
.b1-form {
  display: flex;
  flex-direction: column;
  position: relative;
}

