  width: auto;
  margin: 0;
  font-size: 16px;
  line-height: 50px;
  border: none !important;
}

.ub-nav .nav li a:hover > i {
  color: #52575b;
}

.ub-nav .nav.nav-sub {
  background-color: #f4f4f4;
  height: 0;
  margin-left: -20px;
  overflow: hidden;
  opacity: 0;
  display: none;
  -webkit-transition: all 0.2s ease-in-out 0s;
  transition: all 0.2s ease-in-out 0s;
}

.ub-nav .nav .active > .nav-sub,
